@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Oxygen:300,400&display=swap);
:root {
  --headfont: 'Fira Sans', sans-serif;
  --bodyfont: 'Oxygen', sans-serif;
  --white: #ffffff;
  --offwhite: #f9f9fc;
  --darkgray: #1b1d1e;
  --gray: #4D4D4D;
  --lightgray: #999999;
  --green: #C2E15F;
  --orange: #FDA333;
  --purple: #D3A4F9;
  --red: #fb5344;
  --pinkred: #FB4485;
  --blue: #6CE0F1;
  --salmon: #fa8a90;
  --salmon-light: #fa9ba1;
  --cyan : #8afaf4;
  --shadow: #cccccc;

  color: #4D4D4D;

  color: var(--gray);
  background-color: #f9f9fc;
  background-color: var(--offwhite);

  font-family: 'Oxygen', sans-serif;

  font-family: var(--bodyfont);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  padding: 0;
  margin: 0;
}

html, body {
  width: 100%;
  min-height: 100%;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6, th {
  font-family: 'Fira Sans', sans-serif;
  font-family: var(--headfont);
  margin-bottom: 0.5rem;
}

h1 { font-size: 3.0rem; }
h2 { font-size: 2.5rem; }
h3 { font-size: 2.0rem; }
h4, th { font-size: 1.5rem; }
h5 { font-size: 1.0rem; }
h6 { font-size: 0.5rem; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  padding: 3rem 1.5rem;
}

ul, ol {
  margin-left: 1rem;
}


.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  padding: 2rem 1rem;
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  border-radius: 10px;
  margin: 0.5rem 1rem;
  min-width: 250px;
}

.card h4 {
  text-align: center;
}

.VisualPage {
  width: 95vw;
  max-width: 1250px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto auto auto;
  grid-template-areas: "header header" "about complexity" "controls controls" "visual visual";
}

@media only screen and (max-width: 750px) {
  .VisualPage {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto auto auto;
    grid-template-areas: "header" "about" "controls" "visual" "complexity";
  }
}

.VisualPage > h1 {
  grid-area: header;
  text-align: center;
  border-bottom: 1px solid var(--darkgray);
}

.VisualPage .About {
  grid-area: about;
}

.VisualPage .Complexity {
  grid-area: complexity;
}

.VisualPage .Complexity table {
  margin: 0 auto;
}

.VisualPage .Complexity th {
  padding-bottom: 0.25em;
  border-bottom: 1px solid var(--darkgray);
}

/* Prevent wrapping of second column i.e. the complexity function */
.VisualPage .Complexity td:nth-of-type(2) {
  white-space: nowrap;
}

.VisualPage .Controls {
  grid-area: controls;
  display: flex;
  flex-direction: column;
}

.VisualPage .Controls .ControlGroup {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.VisualPage .Controls .ControlGroup * {
  margin: auto 0.5rem;
}

.VisualPage .Visual {
  grid-area: visual;
  display: flex;
  min-height: auto;
}

.linked-list {
  width: 100%;
  display: flex;
}

.linked-list-node {
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  padding: 1.5rem 0.75rem;
  margin: auto 1rem;
  border-radius: 5px;
  transition: transform ease-in-out 0.25s;
  position: relative;
}

.linked-list-node::after {
  display: block;
  content: '';
  background-color: var(--gray);
  width: 2rem;
  position: absolute;
  height: 1rem;
  left: 100%;
  top: calc(50% - 0.5rem);
  -webkit-clip-path: polygon(0% 37.5%, 40% 37.5%, 40% 0%, 65% 37.5%, 100% 37.5%, 100% 62.5%, 65% 62.5%, 40% 100%, 40% 62.5%, 0% 62.5%);
          clip-path: polygon(0% 37.5%, 40% 37.5%, 40% 0%, 65% 37.5%, 100% 37.5%, 100% 62.5%, 65% 62.5%, 40% 100%, 40% 62.5%, 0% 62.5%);
}

.linked-list-node:last-child::after {
  display: none;
}

.linked-list-node.highlight {
  transform: scale(1.25);
}

.linked-list-node.highlight::before, .linked-list-node.highlight::before {
  transform: scale(0.8) translateX(-4px);
}

.stack {
  display: flex;
  width: 50%;
  min-height: 20rem;
  padding-top: 5rem;
  border: 5px solid var(--gray);
  border-top: none;
  flex-direction: column-reverse;
  flex-wrap: nowrap;
  margin: 0 auto;
}

.stack-node {
  overflow: hidden;
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  margin: 0.5rem 1rem;
  padding: 1rem 0.5rem;
  border-radius: 4px;
  transition: all 0.25s ease-out;
  text-align: center;
}

.stack-node-appear {
  opacity: 0;
  transform: translateY(-50px);
}

.stack-node-appear-active {
  opacity: 1;
  transform: translateY(0);
}

.stack-node-exit {
  opacity: 1;
  transform: translateY(0);
}

.stack-node-exit-active {
  opacity: 0;
  transform: translateY(-50px);
}

.queue {
  display: flex;
  width: 100%;
  height: 75px;
  border-top: 5px solid var(--gray);
  border-bottom: 5px solid var(--gray);
  flex-direction: row-reverse;
  flex-wrap: nowrap;
}

.queue-node {
  background-color: var(--white);
  box-shadow: 0px 0px 10px var(--shadow);
  margin: 0.5rem 1rem;
  padding: 1rem 0.5rem;
  border-radius: 4px;
  transition: all 0.25s ease-out;
}

.queue-node-appear {
  opacity: 0;
  transform: translateX(-500%);
}

.queue-node-appear-active {
  opacity: 1;
  transform: translateX(0);
}

.queue-node-exit {
  opacity: 1;
  transform: translateY(0);
}

.queue-node-exit-active {
  opacity: 0;
  transform: translateY(50px);
}

.NavBar {
  width: 100vw;
  height: 3rem;
  background-color: var(--pinkred);
  margin-bottom: 1rem;
}

.NavBar nav {
  display: flex;
  height: inherit;
  align-items: center;
}

.NavBar a {
  position: relative;
  text-align: center;
  padding: 0 0.5rem;
  margin: 0 0.5rem;
  text-decoration: none;
  color: var(--white);
  font-family: var(--headfont);
  font-size: 1.5rem;
  transition: color ease-in-out 0.2s;
}

.NavBar a:hover {
  color: var(--offwhite);
}

.NavBar a:after {
  display: block;
  content: '';
  position: absolute;
  height: 2px;
  left: 50%;
  right: 50%;
  bottom: -0.25rem;
  background-color: var(--darkgray);
  transition: all ease-in-out 0.2s;
}

.NavBar a:hover:after {
  left: 0;
  right: 0;
}


